<template>
    <div class="Qdao">
         <van-nav-bar
      title="提交订单"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
   
   <van-button type="info" class="btn" round @click="ann">每日签到</van-button>
   <van-calendar v-model="show" :min-date="minDate"  @confirm="confirm" @select="select" :color="col" type="multiple" />
    </div>

</template>

<script>
   export default {
  data() {
    return {
      show: false,
      minDate: new Date(),
      maxDate: new Date(2020, 12, 31),
      col:'lightblue'
    
    };
  },
  methods: {
   
    back(){
        this.$router.go(-1);
    },
    ann(){
        this.show = true;
    },
    confirm(){
        this.show = false;
    },
    select(){
   this.$toast.success('签到成功');
   this.col = 'blue';
    }
   
  },
};
</script>

<style lang="less" scoped>
.btn{
   margin-top: 100px;
   margin-left: 40%;
}
</style>